        <!--企业地址-->
        <div id="address">{{$store.state.service.serviceData.address}}</div>
        <!--电话-->
        <div id="telphone">{{$store.state.service.serviceData.telno}}</div>
        <!--银行账号-->
        <div id="banknumber">{{$store.state.service.serviceData.bank_code}}</div>
        <!--上一步-->
        <div id="init3-pre" @click="init_next(1)"></div>
        <!--下一步-->
        <div id="init3-next" @click="init_next(3)"></div>
      </div>
      <div id="init4">
        <!--上一步-->
        <div id="init4-pre" @click="init_next(2)"></div>
        <!--下一步-->
        <div id="init4-next" @click="init_next(4)"></div>
      </div>
      <div id="init5">
