        <div id="init5-finish" @click="finish()"></div>
      </div>
    </div>
    <div id="indexSet-pop-content" class="layui-layer-wrap" style="display: none;">
      <div class="indexSet-pop-content-menu">
        <div :class="i===menuTextNumber?'active menu-text1':'menu-text1'" v-for="(item, i) in menuText1" :key="i"
