<script>
  export default {
    data () {
      return {
        menuText1: ['基本信息',
          '凭证接口',
          '上传设置',
          '其他设置',
          '委托代办退税'],
        menuTextNumber: 0
      }
    },
    methods: {
      pop_init () {
        layer.closeAll()
        layer.open({
          type: 1,
          title: ['系统参数设置', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['648px'], //宽高
          content: $('#init1'),   //内容
          resize: false,   //不允许拉伸
          shade: [0.1, '#3b6ea5']   //遮罩阴影
        })
      },
      init1_exit () {
        layer.closeAll()
      },
      init_next (num) {
        layer.closeAll()
        layer.open({
          type: 1,
          title: ['系统参数设置', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['648px'], //宽高
          content: $('#init' + (num + 1)),   //内容
          resize: false,   //不允许拉伸
          shade: [0.1, '#3b6ea5']   //遮罩阴影
        })
      },
      finish () {
        layer.closeAll()
      },
      pop_set () {
        //页面层
        layer.open({
          type: 1,
          title: ['系统参数设置', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['604px'], //宽高
          content: $('#indexSet-pop-content'),   //内容
          resize: false,   //不允许拉伸
          shade: [0.1, '#3b6ea5']   //遮罩阴影
        })
      },
      menuSelect (i) {
        this.menuTextNumber = i
      }
    }
  }


</script>

